.form {
    text-align: center;
    margin: 20px;
    font-size: xx-large;
}

.container {
    margin: 3%;
}

input[type=text], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
  }

li a {
    display: block;
    text-align: center;
    padding: 16px;
    text-decoration: none;
    color: white;
}

li {
    float: right;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: black;
}

.main {
    width: 65%;
    float: right;
}

.menu {
    width: 34%;
    float: left;
}

.border {
    border: 2px solid black;
    height: 4000px;
}

.list-item-border {
    list-style-type: none;
    padding: 20px;
    width: 100%;
}

.d-none {
    display: none;
}

.background {
    background-color: cornflowerblue;
}

.body {
    background-color: cornflowerblue;
}

.menu-title {
    text-align: center;
}

.container-text {
    margin-left: 25%;
    margin-right: 25%;
}